body {
  margin: 0;
  border: 0;
  display: flex;
  flex-direction: column;
  font-family: "Montserrat Subrayada", sans-serif;
}
.all {
  width: 100%;
  height: 100%;
}
button {
  margin: 10px;
  background-color: aquamarine;
  font-family: "Electrolize", sans-serif;
  font-size: 18px;
}
button:hover {
  background-color: white;
}
.header {
  position: relative;
  height: 50px;
  display: flex;
  justify-content: space-between;
}
.menu {
  margin-top: 20px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
.tabname {
  min-width: 10vw;
  border-left: solid 1px gray;
  border-top: solid 1px gray;
  border-right: solid 1px gray;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  padding: 15px;
  color: gray;
  -webkit-box-shadow: 1px -2px 7px 3px rgba(0, 0, 0, 0.5);
  -moz-box-shadow: 1px -2px 7px 3px rgba(0, 0, 0, 0.5);
  box-shadow: 1px -2px 7px 3px rgba(0, 0, 0, 0.5);
  cursor: pointer;
}
.tabnameactive {
  min-width: 10vw;
  border-left: solid 1px gray;
  border-top: solid 1px gray;
  border-right: solid 1px gray;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  padding: 15px;
  background-color: rgb(239, 253, 249);
  -webkit-box-shadow: 2px -2px 7px 3px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 2px -2px 7px 3px rgba(0, 0, 0, 0.75);
  box-shadow: 2px -2px 7px 3px rgba(0, 0, 0, 0.75);
  cursor: pointer;
}
.maincontainer {
  border-top: 1px solid gray;
  overflow: scroll;
  height: 100%;
}

img {
  object-fit: cover;
  max-width: 100%;
  max-height: 100%;
}
.greeting {
  padding-left: 10px;
  padding-top: 10px;
  font-family: "Electrolize", sans-serif;
  font-size: 18px;
}
.fc-scrollgrid-sync-table {
  height: 550px !important;
}
.fc-daygrid {
  height: 580px !important;
}
.fc {
  height: 650px !important;
}
